* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff4f7;
    height: 100%;
}

html,
body,
#root,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width:100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 50px;
    width: 80%;
    padding-top:70px;
}

.movies .movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding:20px;
    color: #ADADbc;
    
}
.movie img{
    position: relative;
    top: -50px;
    max-width: 180px;
    width:100%;
    margin-right: 30px;
    padding: 20px;
}

.movie .movie_title  ,
.movie .movie_year{
    font-weight: 300;
    margin: 0;
}

.movie .movie_title  {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .moive_genres {
  list-style: none;
  padding: 0;
  margin:0;
  display: inline;
  margin:5px 0px;
}

.moive_genres li{
    margin-right: 10px;
    font-size: 24px;
}